const Welcome = () => <>
  <h1>Welcome</h1>
  <p>Content will come soon.</p>
  <p>
  </p>
</>;

const GameEntry = () => <p><Link to="/intro">Wanna play a game in the meantime?</Link></p>

class Intro extends React.Component {
  state = {
    playersCount: 1
  };

  handleSelect(option) {
    this.setState({
      playersCount: option
    })
  }

  render() {
    const { playersCount } = this.state;
    return (
      <>
        <p>Some info here <Link to="/game">Ready!</Link></p>
        <select onChange={(e) => this.handleSelect(e.target.value)}>
          <option value="1" selected>1 player</option>
          <option value="2">2 players</option>
        </select>
        <Link to={`/game/${playersCount}`}>Ready!</Link>
      </>
    );
  }
}

const App = () => (
  <>
    <ThemeProvider theme={theme}>
      <Global
        styles={globalStyles}
      />
      <Router>
        <div
          css={
            css`
              display: flex;
              justify-content: center;
              align-items: center;
              flex-direction: column;
            `
          }
        >
          <Welcome />
          <Route path="/" exact component={GameEntry} />
          <Route path="/intro" component={Intro} />
          <Route path="/game/:players" render={(props) => (
            <Game numberOfPlayers={props.match.params.players} />
          )} />
          <Palm />
        </div>
      </Router>
    </ThemeProvider>
  </>
);

export default App;